* {
  padding: 0;
  margin: 0;
}

ul, li {
  list-style: none;
}

a {
  color: cccccc;
  text-decoration: none;
}

.wrap {
  width: 1280px;
  margin: 30px auto;
}

.login {
  width: 1280px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.login > a {
  margin-right: 10px;
}

.logo {
  margin: 15px 0 0 25px;
  width: 150px;
  display: inline-block;
}

.logo > h1 {
  width: 150px;
  font-size: 30px;
  color: #7469F0;
  font-weight: bold;
}

.nav {
  margin: 15px 0 35px 110px;
  width: 750px;
  display: inline-block;
}

.nav > ul {
  width: 700px;
  display: flex;
  flex-direction: row;
  text-align: center;
  font-size: 20px;
}

.nav > ul > li {
  margin-left: 100px;
}

.nav .navSub {
  display: none;
  width: 120px;
  font-size: 16px;
  margin: 20px auto;
}

.nav .navSub > li {
  margin-top: 10px;
}

.mainSlider > .sliderWrap {
  width: 1280px;
  height: 360px;
  overflow: hidden;
}

.mainSlider > .sliderWrap > .sliderImg {
  width: 3840px;
  animation: sliderLoop 6s infinite;
}

.mainSlider > .sliderWrap > .sliderImg > img {
  width: 1280px;
}

@keyframes sliderLoop {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(-1280px);
  }
  100% {
    transform: translateX(-2560px);
  }
}/*# sourceMappingURL=App.css.map */